import React, { useEffect, useState } from "react";

const ProfileStatusWithHooks = (props) => {

  let [editMode, setEditMode] = useState(false);
  let [status, setStatus] = useState(props.status);

  useEffect(() => { // выполнится после того, как снаружи придёт новый статус 
    setStatus(props.status)
  }, [props.status]) // подписывается на изменение props.status, если изменяется - вызывается setStatus(props.status)

  const activateEditMode = () => {
    setEditMode(true);
  };

  const deactivateEditMode = () => {
    setEditMode(false);
    props.updateStatus(status); // меняем статус, сетаем его на серваке
  };

  const onStatusChange = (e) => {
    setStatus(e.currentTarget.value);
  }

  return <>
      {editMode ? <input onChange={onStatusChange} onBlur={deactivateEditMode} autoFocus={true} value={status} /> 
      : <span onClick={activateEditMode}>{props.status ? props.status : '---'}</span>}
  </>
}

export default ProfileStatusWithHooks;
